@import "./variables-colors";

// defaults
$rem-base: 20px;
$gutter-size: $rem-base;
$bottom-spacing: 36px;
$border-layout: 3px solid $cl-blackest;
$responsive-spacing-top: $gutter-size * 2;
$transition-time: .2s;
$default-border-size: 1px;
$base-font-size: $gutter-size;

// fonts
$path-fonts: "../fonts";
$font-fallback: arial, sans-serif;
$xsmall-font-size: 10px;
$small-font-size: 12px;
$medium-font-size: 14px;
$normal-font-size: 16px;
$large-font-size: 20px;
$xlarge-font-size: 24px;
$xxlarge-font-size: 32px;

// padding and margin values for content
$padding-default: $gutter-size;
